import React, {useEffect, useState} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CSmartTable
} from '@coreui/react-pro'
import CIcon from '@coreui/icons-react'
import { cilDelete, cilEyedropper, cilLockLocked, cilLowVision, cilPen, cilUser, cilViewColumn, cilViewModule, cilViewQuilt } from '@coreui/icons'
import axios from 'axios'
import { ToastContainer, toast } from 'react-toastify'

const Clients = () => {
  const navigate = useNavigate();
  const [clients, setClients] = useState([])

  useEffect(() => {
    axios.get('client/findAll', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => setClients(response.data.data))
    .catch(error => console.error('error fetching clients', error));
  }, [])

  const deleteClientAction = (e, clientId, name) => {
    e.preventDefault();
    const confirmDelete = window.confirm(`You are about to Delete ${name} and it will not be able to be retrieved. Are you sure?`);
    if (confirmDelete) {
      axios.delete(`/client/delete/${clientId}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      })
      .then((response) => {
        // Reload the page or update state as needed
        window.location.reload();
      })
      .catch((error) => {
        console.error('Error deleting product:', error);
      });
    } else {
      // Handle cancellation if needed
      console.log('Deletion canceled');
    }
  }

  const columns = [
    {
      key: 'companyName',
      label: 'Company Name',
    },
    {
      key: 'clientType',
      label: 'Company Type',
    },
    {
      key: 'contact',
      label: 'Contact Person',
    },
    {
      key: 'actions',
      label: 'Actions',
      filter: false,
      sorter: false,
      _style: { width: '20%' }

    },
  ]

  return (
    <>
    <div className="bg-body-tertiary min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
        <CCol xl={12}>
          <CCard className="mb-4">
            <CCardBody className="p-4">
        <CRow className='p-4'>
          <CCol sm={5}><h4>Companies</h4></CCol>
          <CCol sm={7} className='d-none d-md-block'>
          <Link to="/addCompany">
            <CButton className='btn btn-primary float-end' type='button'>Set Company</CButton>
          </Link>
          </CCol>
        </CRow>
              <CCol md={12}>
              <CSmartTable
        activePage={2}
        cleaner
        clickableRows
        columns={columns}
        columnFilter
        columnSorter
        items={clients}
        itemsPerPageSelect
        itemsPerPage={5}
        pagination
        onFilteredItemsChange={(items) => {
          console.log(items)
        }}
        onSelectedItemsChange={(items) => {
          console.log(items)
        }}
        scopedColumns={{
          contact: (item) => (
            <td>
              {(item.contact !== null)? item.contact.firstName+' '+item.contact.lastName: 'N/A'}
            </td>
          ),
          actions: (item) => (
            <td>
              <div onClick={(e) => navigate("/editCompany", {state:{clientId: item.id}})}>
                <CIcon icon={cilPen} className="me-2" />
              </div>
                <CIcon icon={cilDelete} className="me-2" onClick={(e)=>deleteClientAction(e, item.id, item.companyName)} />
            </td>
          ),
        }}    
        selectable
        sorterValue={{ column: 'createdAt', state: 'desc' }}
        tableFilter
        tableProps={{
          className: 'add-this-class',
          responsive: true,
          striped: true,
          hover: true,
        }}
        tableBodyProps={{
          className: 'align-middle'
        }}
      />
              </CCol>
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
      </CContainer>
    </div>
    </>
  )
}

export default Clients
